import { filter, first, Fn, ReplaySubject, shareReplay } from "@hypertype/core";
import { IWebSocketService } from "./i-web-socket.service";
import { LogLevel } from "./signalr/contracts/ILogger";
import { HubConnectionBuilder } from "./signalr/HubConnectionBuilder";
export class BaseWebSocketService extends IWebSocketService {
    constructor(url = "http://localhost:8888/inventory", api, tokenStore) {
        super();
        this.url = url;
        this.api = api;
        this.tokenStore = tokenStore;
        this.connectionSubject$ = new ReplaySubject(1);
        this.connection$ = this.connectionSubject$.asObservable().pipe(filter(Fn.Ib));
        this.isInit = false;
    }
    Hub(hub) {
        if (!this.isInit)
            this.initConnection();
        return new WebSocketHub(this.connection$, hub);
    }
    send(method, ...params) {
        return this.connection$.pipe(first()).toPromise()
            .then(connection => connection.start().then(() => {
            return connection.invoke(method, ...params);
        }));
    }
    initConnection(tryCounter = 0) {
        const config = {
            ...this.getConfig(),
            httpClient: this.api,
            accessTokenFactory: () => {
                return this.tokenStore.get();
            }
        };
        const connection = new HubConnectionBuilder()
            .withUrl(this.url, config)
            .configureLogging({
            log: (type, message) => {
                if (type >= LogLevel.Error) {
                    connection.stop().catch();
                    // this.restartConnection(connection);
                }
            }
        })
            .build();
        connection.start().catch(e => {
            this.restartConnection(tryCounter + 1);
        });
        connection.onclose(e => {
            this.restartConnection();
        });
        this.connectionSubject$.next(connection);
        this.isInit = true;
    }
    restartConnection(tryCounter = 1) {
        this.connectionSubject$.next(null);
        console.warn('disconnected, try: ', tryCounter);
        setTimeout(() => {
            //   this.initConnection(tryCounter);
        }, tryCounter * 1000);
    }
}
export class WebSocketHub {
    constructor(connection, hub) {
        this.connection = connection;
        this.messagesSubject = new ReplaySubject();
        this.messages$ = this.messagesSubject.asObservable().pipe(shareReplay(1));
        this.connection.subscribe(connection => connection.on(hub, (...params) => {
            this.messagesSubject.next(params);
        }));
    }
}
