import { catchError, fromEvent, InjectionToken, map, mergeMap, of, ReplaySubject, startWith, tap, throwError } from '@hypertype/core';
export const ApiUrlInjectionToken = new InjectionToken('apiUrl');
export class ApiService {
    constructor(http, ApiUrl) {
        this.http = http;
        this.ApiUrl = ApiUrl;
        // protected http: IRequestService;
        this.isBSON = false;
        this.loadingCounter = 0;
        this.loadingSubject$ = new ReplaySubject();
        this.Loading$ = this.loadingSubject$.asObservable().pipe(
        // map(d => d != 0),
        startWith(0));
        this.processResult = (body) => {
            if (!this.isBSON) {
                return of(body);
            }
            const fileReader = new FileReader();
            fileReader.readAsArrayBuffer(body);
            return fromEvent(fileReader, 'loadend').pipe(map(_ => fileReader.result), map(array => new Uint8Array(array)));
        };
    }
    get(url, options = {}) {
        return this.request('GET', url, null, options);
    }
    post(url, body, options = {}) {
        return this.request('POST', url, body, options);
    }
    delete(url) {
        return this.request('DELETE', url);
    }
    put(url, body, options = {}) {
        return this.request('PUT', url, body, options);
    }
    request(method, url, body = null, options = {}) {
        //WTF?
        if (!/accessTime/.test(url)) {
            this.loadingCounter++;
            this.loadingSubject$.next(this.loadingCounter);
        }
        if (!/^(http|ws)s?:\/\//.test(url))
            url = this.ApiUrl.toString() + url;
        const res = this.http.request(method, url, body, {
            headers: {
                ...(options.headers || {}),
                'Accept': this.isBSON ? 'application/bson' : 'application/json'
            },
            params: options.params,
            responseType: options.responseType || (this.isBSON ? 'blob' : 'json')
        }).pipe(
        // filter(event => {
        //   return (event instanceof HttpResponse);
        // }),
        // map(event => (<HttpResponse<Blob>>event).body),
        tap(() => {
            this.loadingCounter--;
            this.loadingSubject$.next(this.loadingCounter);
        }), catchError(e => {
            this.loadingCounter--;
            this.loadingSubject$.next(this.loadingCounter);
            return throwError(e);
        }), mergeMap(this.processResult));
        return res;
    }
}
